.container {
    height: 100%;
    display: flex;
    justify-content: center;
  }

.loader {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
}

.detail {
    display: grid;
    grid-gap: 10px;
    padding: 100px;
    width: 80%;
    padding-top: 70px;
  }